.home {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.column {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding: 0 30px;
    min-width: 100px;
}

.title {
    font-size: 24px;
    text-align: center;
}

.image {
    max-width: 500px;
    max-height: 300px;
}

.image img {
    width: 100%;
    height: 100%;
}

.chevrons {
    display: flex;
    justify-content: center;
}

.chevrons div {
    padding: 0 10px;
}